import React from "react";
import useLocalStorage from "react-use-localstorage";
import { QRCode } from "react-qr-svg";
import "./app.css";

const defaultItemsText = `
🏠客厅 存包处
缝纫研室 拉链纽扣 缝纫机
电子研室 强电线缆 列印角料 
手工研室 胶刀尺笔 缝纫线材	打磨流程 台钳钻架	手持电钻 小缝纫机
列印研室 打印耗材	游标卡尺 列印废料	打印备件
厨房	米面干粮 储备水源	厨房纸巾 储备湿粮	烘培耗材 蛋打发机 微蒸烤箱	储备汤料
仓库	排插线板 储备厨纸	 硬纸板箱	单车配件 泡沫盒子 厨房备件 收纳工具
`.trim();

export function Item({ value }) {
  return (
    <li className="item">
      <QRCode value={value || ""} className="qrcode" />
      <h2>{value}</h2>
    </li>
  );
}
export function Items({ items }) {
  return (
    <ul className="items">
      {items.map((e, i) => (
        <Item value={e} key={e + i} />
      ))}
    </ul>
  );
}
export default function App() {
  const isBrowser = () => typeof window !== "undefined";
  return isBrowser() ? _App() : "";
}
function _App() {
  const [itemsText, setItemsText] = useLocalStorage(
    "itemsText",
    defaultItemsText
  );
  const change = (e) => setItemsText?.(e.currentTarget.value);
  const items = itemsText?.trim()?.split(/\s+/) || [];
  return (
    <div className="root">
      <div className="control">
        <h1>雪星仓储管理系统：标签生成器</h1>
        <h2>使用方法</h2>
        <ol>
          <li>在编辑框写好相应物品的，用空格或换行分割</li>
          <li>
            按 Ctrl+P 将下方标签打印到背胶纸上，你可以调整到你喜欢的 Scale。
          </li>
          <li>按需用透明胶或者热敏覆膜把打印表面保护起来</li>
          <li>裁剪</li>
          <li>贴在你需要的地方，比如各种物品或盒子表面</li>
        </ol>
      </div>
      <div className="control">
        <textarea onChange={change} value={itemsText} />
      </div>
      <div className="print">
        <Items items={items} />
      </div>
    </div>
  );
}
